.super-games__data p {
  font-size: 14px;
  line-height: 21px;
  margin: 0;
  text-align: start;
}
@media (max-width: 1199px) {
  .super-games__data p {
    font-size: 10px;
    line-height: 14px;
    margin: 0;
  }
}
@media (max-width: 767px) {
  .super-games__data p {
    margin: 0;
  }
}

.super-games__image {
  object-fit: contain;
}

